@font-face {
  font-family: 'nes-icons';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/nes-icons.eot");
  src: url("../fonts/nes-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/nes-icons.woff2") format("woff2"), url("../fonts/nes-icons.woff") format("woff"), url("../fonts/nes-icons.ttf") format("truetype"), url("../fonts/nes-icons.svg#nes-icons") format("svg");
