.nes-icon {
  display: inline-block;
  font-family: 'nes-icons';
  font-weight: 400;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: none;
}

.nes-icon.rotate-90 {
  transform: rotate(90deg);
  transform-origin: 50% 50%;
}

.nes-icon.rotate-180 {
  transform: rotate(180deg);
  transform-origin: 50% 50%;
}

.nes-icon.rotate-270 {
  transform: rotate(270deg);
  transform-origin: 50% 50%;
}

.nes-icon.flip-horizontal, .nes-icon.flip-vertical {
  transform-origin: 50% 50%;
}

.nes-icon.flip-horizontal {
  transform: scale(-1, 1);
}

.nes-icon.flip-vertical {
  transform: scale(1, -1);
}

